import React, {useEffect, useState} from "react";
import config from "../../../../../../config";
import style from './index.module.css'
import Story from './components/story'
import {NavLink} from "react-router-dom";

let Sound = {
    audio: new Audio() || false,
    settings: {
        defaultMuted: true,
        defaultVolume: .2,
        defaultLoop: true
    },
    set: function (url) {
        url = ''+url
        // const check = await isUrlFound(url)
        // if (!check) return
        if (this.audio.src==="" || this.audio.src===null) {
            this.audio.src = url
            this.audio.volume = this.settings.defaultVolume
            this.settings = {
                ...this.settings,
                volume: this.settings.defaultVolume
            }
            this.audio.loop = this.settings.defaultLoop
            this.audio.muted = this.settings.defaultMuted
            this.audio.play().catch(err => console.log(err))
        } else if (this.audio.src!==url) {
            this.update(url).catch()
        }
    },
    update: async function (url) {
        const delay = (ms) => { return new Promise(resolve => setTimeout(resolve, ms)); }
        const timer = 600
        const volumeDelta = this.audio.volume/(timer*.5)

        for (let i = timer*.5-1; i >= 0; i--) {
            await delay(1)
            let vol = this.audio.volume - volumeDelta
            this.audio.volume = vol<0 ? 0 : vol
        }
        this.audio.pause()
        this.audio.src = url
        this.audio.play().catch()
        for (let i = 0; i <= timer*.5-1; i++) {
            await delay(1)
            let vol = this.audio.volume + volumeDelta
            this.audio.volume = vol<=this.settings.volume ? vol : this.settings.volume
        }
    }
}
let Video = {
    video: null,
    settings: {
        autoPlay: true,
        muted: true,
        controls: false,
        loop: true
    },
    set: function (video) {
        return (
            <video
                muted={video.muted || this.settings.muted}
                autoPlay={video.autoPlay || this.settings.autoPlay}
                controls={video.controls || this.settings.controls}
                loop={video.loop || this.settings.loop}
            >
                <source src={video.src} type={`video/${video.format || 'mp4'}`}/>
            </video>
        )
    }
}

const Index = (props) => {
    const [userWay, setUserWay] = useState([0])
    const [current, setCurrent] = useState({})
    const [content, setContent] = useState(null)
    useEffect(() => {
        // Reset
        setContent(null)

        // Стили
        if (Story[userWay[userWay.length-1]].style && Story[userWay[userWay.length-1]].style.backgroundImage) {
            setCurrent({
                ...current,
                style: {
                    ...current.style,
                    backgroundImage: `url("${Story[userWay[userWay.length-1]].style.backgroundImage}")`
                }
            })
        }

        // Медиа
        // - Аудио
        if (Story[userWay[userWay.length-1]].media && Story[userWay[userWay.length-1]].media.audio) {
            if (Story[userWay[userWay.length-1]].media.audio.src!==null) {
                Sound.set(Story[userWay[userWay.length-1]].media.audio.src)
            }
        }
        // - Видео
        if (Story[userWay[userWay.length-1]].media && Story[userWay[userWay.length-1]].media.video) {
            if (Story[userWay[userWay.length-1]].media.video.src!==null) {
                setContent(Video.set(Story[userWay[userWay.length-1]].media.video))
            }
        }
    }, [userWay])

    return(
        <main style={{backgroundImage:current.style && current.style.backgroundImage && current.style.backgroundImage}}>
            <div className={style.content}>
                {content && content}
            </div>
            <div className={style.cardsList} style={{position:'relative'}}>
                {Story[userWay[userWay.length - 1]].title &&
                    <h3>{Story[userWay[userWay.length - 1]].title}</h3>
                }
                {Story[userWay[userWay.length - 1]].text &&
                    <p>{Story[userWay[userWay.length - 1]].text}</p>
                }
                <div className={style.answersList}>
                    {Story[userWay[userWay.length-1]].answers && Story[userWay[userWay.length-1]].answers.map((button,i) =>
                        <button onClick={() => {setUserWay([...userWay, button.target])}} key={i}>{button.text}</button>
                    )}
                    {userWay.length > 1 &&
                        <button onClick={() => {setUserWay(userWay.slice(0, userWay.length - 1))}}>Вернуться</button>
                    }
                </div>
                <NavLink to={`${config.repo}/`} style={{position:'absolute', bottom:0, left:0}}>home</NavLink>
            </div>
        </main>
    )
}

export default Index